.section {
    padding: 20px 0px 0px;
    text-align: center;
  }
  .title {
    margin: 0px 0px 20px;
    font-size: 24px;
    font-weight: 600;
    text-align: center;
    text-transform: capitalize;
    color: #f8640e;
  }
  .container {
    display: flex;
    flex-wrap: wrap;
  
    overflow: hidden;
  }
  
  .item {
    width: calc((100% - 120px) / 5);
    height: 300px;
    margin-top: 40px;
    margin-right: 30px;
    padding: 20px;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    background-color: rgb(47, 47, 46);
  }
  
  .item:nth-child(5n + 5) {
    margin-right: 0px;
  }
  
  .item:nth-child(-n + 5) {
    margin-top: 0px;
  }
  .imageContainer {
    height: 180px;
  }
  
  .image {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  .name {
    margin: 10px 0px 0px;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
  }
  
  .text {
    margin: 8px 0px 0px;
    font-size: 14px;
    color: #fff;
  }
  